.catalog__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 48px;
  padding-left: 20px;
  padding-right: 20px;
}

@media (max-width: 768px) {
  .catalog__header {
    margin-bottom: 38px;
  }
}

@media (max-width: 659px) {
  .catalog__header {
    flex-wrap: wrap;
  }
}

@media (max-width: 360px) {
  .catalog__header {
    margin-bottom: 28px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
