.banner__image {
  img {
    width: 100%;
    height: 379px;
    object-fit: cover;
    vertical-align: bottom;
  }
}

@media (max-width: 768px) {
  .banner__image {
    img {
      height: 359px;
    }
  }
}

@media (max-width: 400px) {
  .banner__image {
    img {
      height: 496px;
    }
  }
}
