.advantages__title {
  font-family: Rubik, sans-serif;
  font-size: 36px;
  line-height: 43px;
  font-weight: 700;
  color: #1a212f;
  text-align: center;
  margin-bottom: 60px;
}

@media (max-width: 768px) {
  .advantages__title {
    margin-bottom: 51px;
  }
}

@media (max-width: 320px) {
  .advantages__title {
    font-size: 28px;
    line-height: 33px;
    margin-bottom: 33px;
  }
}
