.modal__window {
  width: 570px;
  margin: 30px auto;
  background-color: #fffefd;
  box-shadow: 0 0 25px rgba(153, 153, 153, 0.25);
  border-radius: 10px;
}

@media (max-width: 768px) {
  .modal__window {
    width: 502px;
  }
}

@media (max-width: 576px) {
  .modal__window {
    width: 290px;
  }
}
