.social-icons__item {
  position: relative;
  margin-right: 20px;
  list-style: none;

  &:last-child {
    margin-right: 0;
  }

  img {
    width: 22px;
    height: 22px;
    vertical-align: bottom;
  }
}

@media (max-width: 768px) {
  .social-icons__item {
    img {
      width: 24px;
      height: 24px;
    }
  }
}
