.card-brief__list-item {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #1a212f;
  margin: 0 0 15px 0;
  padding: 0 0 0 21px;

  &:last-child {
    margin: 0;
  }

  &::before {
    content: "";
    position: absolute;
    top: 3px;
    left: 0;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #fac663;
  }
}

@media (max-width: 1050px) {
  .card-brief__list-item {
    margin: 0 0 10px 0;
    padding: 0 0 0 20px;
    font-size: 15px;
    line-height: 18px;

    &::before {
      width: 11px;
      height: 11px;
    }
  }
}

@media (max-width: 720px) {
  .card-brief__list-item {
    margin: 0 0 10px 0;
    padding: 0 0 0 20px;
    font-size: 15px;
    line-height: 18px;

    &::before {
      width: 11px;
      height: 11px;
    }
  }
}
