.modal_view_filter {
  .modal {
    &__window {
      width: 332px;
      margin: 130px auto 0 auto;
      box-shadow: 0 4px 25px rgba(153, 153, 153, 0.25);
      border-radius: 6px;
    }

    &__close-btn {
      position: absolute;
      top: 24px;
      right: 24px;
      width: 16px;
      height: 16px;
    }

    &__title {
      padding-top: 50px;
    }
  }
}

@media (max-width: 320px) {
  .modal_view_filter {
    .modal {
      &__window {
        width: 290px;
        margin: 102px auto 0 auto;
      }
    }
  }
}
