.rooms__slider {
  max-width: 1030px;
  margin: 0 auto;

  .slider {
    position: relative;
  }

  .swiper {
    &-container {
      overflow: hidden;
    }

    &-wrapper {
      display: flex;
    }

    &-slide {
      flex-shrink: 0;
    }

    &-pagination {
      margin-top: 47px;
      text-align: center;

      &-bullet {
        width: 12px;
        height: 12px;
        border-radius: 6px;
        background-color: #e1e1e1;
        margin-right: 12px;

        &:last-child {
          margin-right: 0;
        }

        &-active {
          background-color: #fac663;
        }
      }
    }

    &-navigation {
      position: absolute;
      right: 0;
      bottom: -12px;
    }

    &-button {
      width: 36px;
      height: 36px;
      border-radius: 18px;
      box-shadow: 0 2px 12px rgba(173, 152, 143, 0.25);
      background-color: #fff;
      background-position: center center;
      background-repeat: no-repeat;

      &-next {
        transform: rotate(180deg);
        background-image: url("/img/arrow.svg");
        box-shadow: 0 -2px 12px rgba(173, 152, 143, 0.25);
      }

      &-prev {
        margin-right: 8px;
        background-image: url("/img/arrow.svg");
      }
    }
  }
}

@media (max-width: 1050px) {
  .rooms__slider {
    max-width: 678px;

    .swiper {
      &-navigation {
        display: none;
      }

      &-pagination {
        margin-top: 42px;
      }
    }
  }
}

@media (max-width: 720px) {
  .rooms__slider {
    max-width: 290px;

    .swiper {
      &-navigation {
        display: none;
      }

      &-pagination {
        margin-top: 22px;
      }
    }
  }
}
