.contacts__map {
  img {
    width: 100%;
    height: 580px;
    object-fit: cover;
    vertical-align: bottom;
  }
}

@media (max-width: 840px) {
  .contacts__map {
    img {
      height: 522px;
    }
  }
}

@media (max-width: 500px) {
  .contacts__map {
    img {
      height: 300px;
    }
  }
}
