.modal__close-btn {
  position: absolute;
  top: 36px;
  right: 36px;
  width: 24px;
  height: 24px;
  background-color: rgba(255, 254, 253, 0);
  background-image: url("/img/close-btn.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

@media (max-width: 768px) {
  .modal__close-btn {
    top: 21px;
    right: 21px;
  }
}

@media (max-width: 576px) {
  .modal__close-btn {
    top: 15px;
    right: 15px;
  }
}
