.filter-button {
  width: 185px;
  height: 42px;
  border: 2px solid #fac663;
  padding: 0;
  margin-bottom: 15px;
  background-image: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #1a212f;
  text-align: center;
  border-radius: 25px;
  background-color: #fffefd;
}

@media (max-width: 320px) {
  .filter-button {
    width: 240px;
  }
}
