
.tooltip {
  display: none;
  position: relative;
  z-index: 1;
  height: 30px;
  border: 1px solid #fac663;
  padding: 0.7em;
  font-family: Lato, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  color: #1a212f;
  background: #fff;
  box-shadow: 0 4px 12px rgba(173, 152, 143, 0.18);
  border-radius: 4px;

  &[data-show] {
    display: block;
  }
}
