.tooltip__arrow, .tooltip__arrow::before {
  position: absolute;
  z-index: -1;
  width: 12px;
  height: 12px;
  background-color: #fff;
}

.tooltip__arrow::before {
  content: "";
  transform: rotate(-45deg);
  border-left: 1px solid #fac663;
  border-bottom: 1px solid #fac663;
}

.tooltip {
  &[data-popper-placement^="top"] > &__arrow {
    bottom: -6px;
  }

  &[data-popper-placement^="bottom"] > &__arrow {
    top: -6px;

    &::before {
      transform: rotate(135deg);
    }
  }

  &[data-popper-placement^="left"] > &__arrow {
    right: -6px;

    &::before {
      transform: rotate(-135deg);
    }
  }

  &[data-popper-placement^="right"] > &__arrow {
    left: -7px;

    &::before {
      transform: rotate(45deg);
    }
  }
}
