.rooms__title {
  font-family: Rubik, sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 43px;
  margin-bottom: 61px;
  text-align: center;

  a {
    text-decoration: none;
    color: #1a212f;
  }
}

@media (max-width: 768px) {
  .rooms__title {
    margin-bottom: 51px;
  }
}

@media (max-width: 320px) {
  .rooms__title {
    margin-bottom: 33px;
    font-size: 28px;
    line-height: 33px;
  }
}
