.card-brief__body {
  position: absolute;
  right: 0;
  width: 100%;
  max-width: 500px;
  height: 284px;
  // padding-left: 100px;
  // padding-top: 34px;
  padding: 34px 100px 40px 100px;
  background: #fff;
  box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
  border-radius: 8px;
  // border: 1px solid red;
  & a {
    text-decoration: none;
  }
}

@media (max-width: 1050px) {
  .card-brief__body {
    max-width: 345px;
    height: 266px;
    padding: 34px 40px 40px 40px;
  }
}

@media (max-width: 720px) {
  .card-brief__body {
    position: static;
    max-width: 290px;
    height: 252px;
    padding: 20px 25px 25px 25px;
    font-size: 15px;
    line-height: 18px;
  }
}
