$height: 50px;

.ok-button {
  width: 122px;
  height: $height;
  padding-left: 34px;
  // border-radius: 25px;
  border-radius: $height / 2;
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
  text-align: left;
  color: #fff;
  background-color: #ff7236;
  background-image: url("/img/paw-alt.svg");
  background-repeat: no-repeat;
  background-position: right 7px center;
  box-shadow: 0 5px 25px rgba(26, 33, 47, 0.1);
}
