.modal_view_smart {
  .modal {
    &__window {
      width: 100%;
      height: 100%;
      margin: 0;
      box-shadow: none;
      border-radius: 0;
      background: #fac663;
    }

    &__close-btn {
      position: absolute;
      top: 24px;
      right: 24px;
      width: 16px;
      height: 16px;
    }

    &__title {
      padding: 0;
    }

    &__body {
      padding: 0;
    }
  }
}
