.contacts__text {
  font-weight: 400;
  font-size: calc(15px + (16 - 15) * (100vw / 1366));
  // font-size: 16px;
  line-height: 19px;
  color: #1a212f;
  margin-bottom: 27px;

  a {
    text-decoration: none;
    color: inherit;
  }
}

@media (max-width: 768px) {
  .contacts__text {
    margin-bottom: 24px;
    font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (1366 - 320)));
    // font-size: 15px;
    line-height: 18px;
  }
}
