.field-checkbox_view_catalog {
  margin-bottom: 29px;

  &:last-of-type {
    margin-bottom: 36px;
  }

  .field-checkbox {
    &__title {
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      color: #1a212f;
      margin-bottom: 14px;
    }

    &__name {
      padding-left: 30px;
    }

    &__name-text {
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #1a212f;

      &::before {
        top: 2px;
        width: 18px;
        height: 18px;
        border: 1px solid #e1e1e1;
        border-radius: 0;
      }

      @at-root input:checked ~ .field-checkbox__name-text::before {
        background-image: url("/img/catalog/checked.svg");
        background-repeat: no-repeat;
        background-position: center center;
      }
    }
  }

  .field-checkbox__input-wrap + .field-checkbox__input-wrap {
    margin-top: 11px;
  }
}
