.booking-button {
  width: 206px;
  height: 50px;
  padding-left: 30px;
  border-radius: 25px;
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
  text-align: left;
  color: #fff;
  background-color: #ff7236;
  background-image: url("/img/paw-alt.svg");
  background-repeat: no-repeat;
  background-position: right 7px center;
  box-shadow: 0 5px 25px rgba(26, 33, 47, 0.1);
}

@media (max-width: 720px) {
  .card-brief .booking-button {
    width: 240px;
    padding-left: 49px;
  }
}
