.card-brief__photo {
  img {
    vertical-align: bottom;
    border-radius: 10px;
    width: 600px;
    height: 404px;
    // object-fit: cover;
  }
}

@media (max-width: 1050px) {
  .card-brief__photo {
    img {
      width: 442px;
      height: 326px;
    }
  }
}

@media (max-width: 720px) {
  .card-brief__photo {
    img {
      width: 290px;
      height: 192px;
      border-radius: 10px 10px 0 0;
    }
  }
}
