.reviews__slider {
  .slider {
    position: relative;
  }

  .swiper {
    &-container {
      max-width: 1268px;
      overflow: hidden;
      margin-left: auto;
    }

    &-wrapper {
      display: flex;
    }

    &-slide {
      flex-shrink: 0;
    }

    &-pagination {
      margin-top: 54px;
      text-align: center;

      &-bullet {
        width: 12px;
        height: 12px;
        border-radius: 6px;
        background-color: #e1e1e1;
        margin-right: 12px;

        &:last-child {
          margin-right: 0;
        }

        &-active {
          background-color: #fac663;
        }
      }
    }

    &-navigation {
      position: absolute;
      right: 167px;
      bottom: -12px;
    }

    &-button {
      width: 36px;
      height: 36px;
      border-radius: 18px;
      box-shadow: 0 2px 12px rgba(173, 152, 143, 0.25);
      background-color: #fff;
      background-position: center center;
      background-repeat: no-repeat;

      &-next {
        background-image: url("/img/arrow.svg");
        transform: rotate(180deg);
        box-shadow: 0 -2px 12px rgba(173, 152, 143, 0.25);
      }

      &-prev {
        margin-right: 8px;
        background-image: url("/img/arrow.svg");
      }
    }
  }
}

@media (max-width: 768px) {
  .reviews__slider {
    .swiper {
      &-container {
        max-width: 723px;
      }

      &-navigation {
        display: none;
      }

      &-pagination {
        margin-top: 49px;
      }
    }
  }
}

@media (max-width: 450px) {
  .reviews__slider {
    .swiper {
      &-container {
        margin-right: auto;
        max-width: 290px;
      }

      &-navigation {
        display: none;
      }

      &-pagination {
        margin-top: 0;
      }
    }
  }
}
