.catalog__sort {
  align-self: flex-end;
  width: 174px;
  height: 35px;
  background: #fff;

  .custom-select-container {
    box-sizing: border-box;
    position: relative;
    height: 100%;
  }

  .custom-select-container * {
    box-sizing: border-box;
  }

  .custom-select-container.is-disabled {
    opacity: 0.333;
  }

  .custom-select-opener {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 17px;
    background-color: #fff;
    box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);
    border-radius: 25px;
    cursor: pointer;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #1a212f;

    &::after {
      content: "";
      position: absolute;
      top: 14px;
      right: 20px;
      width: 14px;
      height: 8px;
      background-image: url("/img/catalog/arrow-open.svg");
    }
  }

  .custom-select-container select {
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
  }

  .custom-select-panel {
    position: absolute;
    z-index: 1;
    top: 0;
    width: 100%;
    max-height: 0;
    overflow: hidden;
    // padding: 0 0 0 17px;
    padding: 0;
    transition: none;
    background-color: #fff;
    box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);
    border-radius: 4px;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #141312;

    &::after {
      content: "";
      position: absolute;
      top: 14px;
      right: 19px;
      width: 14px;
      height: 8px;
      background-image: url("/img/catalog/arrow-close.svg");
    }
  }

  .custom-select-container.is-open .custom-select-panel {
    height: 139px;
    overflow-y: auto;
  }

  .custom-select-option {
    padding: 0.5em 0.5em 0.5em 17px;
    margin-bottom: 4px;
    cursor: pointer;

    &:first-child {
      margin-top: 2px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  .custom-select-option.has-focus {
    background-color: #f9f2ec;
  }

  .custom-select-option.is-selected::before {
    content: "";
    padding-right: 0;
  }

  .custom-select-optgroup > .custom-select-option {
    padding-left: 2em;
  }

  .custom-select-optgroup::before {
    content: attr(data-label);
    display: block;
    padding: 0.5em;
    color: #888;
  }
}

@media (max-width: 360px) {
  .catalog__sort {
    width: 154px;

    .custom-select-opener {
      &::after {
        right: 9px;
      }
    }
  }
}
