.reviews__title {
  font-family: Rubik, sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 43px;
  color: #1a212f;
  text-align: center;
  margin-bottom: 62px;
}

@media (max-width: 768px) {
  .reviews__title {
    margin-bottom: 52px;
  }
}

@media (max-width: 320px) {
  .reviews__title {
    margin-bottom: 31px;
    font-size: 28px;
    line-height: 33px;
  }
}
