.menu {
  display: flex;
  flex-wrap: wrap;
  max-width: 1009px;
  list-style: none;
  padding-top: 10px;
}

@media (max-width: 576px) {
  .header__navbar .menu {
    display: none;
  }

  .footer__navbar .menu {
    flex-wrap: wrap;
    flex-direction: column;
    align-items: flex-start;
    // margin-bottom: -25px;
    height: 99px;
    padding-top: 22px;
  }
}
