.booking-button_size_big {
  width: 230px;
  height: 46px;
  padding-left: 51px;
  font-size: 14px;
  line-height: 17px;
  background-position: right 5px center;
}

@media (max-width: 768px) {
  .booking-button_size_big {
    width: 274px;
    padding-left: 75px;
  }
}

@media (max-width: 360px) {
  .booking-button_size_big {
    width: 240px;
    padding-left: 58px;
  }
}
