.input-group__input {
  width: 430px;
  height: 52px;
  border: 1px solid #d7d7d7;
  border-radius: 25px;
  padding-left: 30px;
}

.input-group__input:focus ~ .input-group__label, .input-group__input:not(:focus):valid ~ .input-group__label {
  top: -2px;
  font-size: 10px;
}

@media (max-width: 768px) {
  .input-group__input {
    width: 442px;
  }
}

@media (max-width: 576px) {
  .input-group__input {
    width: 260px;
  }
}
