.catalog__title {
  margin: 0;
  padding-right: 30px;
  font-family: Rubik, sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 43px;
  color: #1a212f;
}

@media (max-width: 635px) {
  .catalog__title {
    margin-bottom: 34px;
  }
}

@media (max-width: 320px) {
  .catalog__title {
    font-size: 28px;
    line-height: 33px;
  }
}
