.banner__slogan {
  margin-bottom: 14px;
  font-family: Rubik, sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 40px;
  color: #1a212f;

  span {
    font-size: 56px;
    line-height: 56px;
  }
}

@media (max-width: 400px) {
  .banner__slogan {
    margin-bottom: 5px;
    font-size: 24px;
    line-height: 28px;

    span {
      font-size: 37px;
      line-height: 44px;
    }
  }
}
