.advantage-card {
  flex: 1 1 324px;
  max-width: 370px;
  min-height: 321px;
  margin: 0 15px 20px 15px;
  padding: 30px 30px 10px 30px;
  background: #fff;
  box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
  border-radius: 8px;
}

@media (max-width: 768px) {
  .advantage-card {
    flex: 1 1 290px;
    max-width: 324px;
    min-height: 285px;
    margin: 0 15px 30px 15px;
    padding: 25px 21px 10px 25px;
  }
}

@media (max-width: 320px) {
  .advantage-card {
    min-height: auto;
  }
}
