.contacts__address {
  margin-top: 60px;
  margin-left: auto;
  padding-left: 40px;
  width: 410px;
}

@media (max-width: 840px) {
  .contacts__address {
    margin-top: 52px;
    padding-left: 45px;
    width: 370px;
  }
}

@media (max-width: 576px) {
  .contacts__address {
    margin-top: 33px;
    padding-left: 15px;
    padding-bottom: 40px;
    width: 320px;
  }
}

@media (max-width: 510px) {
  .contacts__address {
    padding-left: 40px;
    width: 410px;
  }
}

@media (max-width: 320px) {
  .contacts__address {
    padding-left: 15px;
    width: 320px;
  }
}
