.contacts__title {
  margin-bottom: 38px;
  font-family: Rubik, sans-serif;
  font-size: calc(28px + (36 - 28) * (100vw / 1366));
  // font-size: 36px;
  line-height: 43px;
  font-weight: 700;
  color: #1a212f;
}

@media (max-width: 768px) {
  .contacts__title {
    margin-bottom: 28px;
    font-size: 36px;
  }
}

@media (max-width: 576px) {
  .contacts__title {
    margin-bottom: 31px;
    font-size: calc(28px + (36 - 28) * ((100vw - 320px) / (1366 - 320)));
    // font-size: 28px;
    line-height: 33px;
  }
}
