.send-button {
  width: 230px;
  height: 50px;
  padding-left: 32px;
  border-radius: 25px;
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
  text-align: left;
  color: #fff;
  background-color: #ff7236;
  background-image: url("/img/paw-alt.svg");
  background-repeat: no-repeat;
  background-position: right 7px center;
  box-shadow: 0 5px 25px rgba(26, 33, 47, 0.1);
}

@media (max-width: 576px) {
  .send-button {
    width: 260px;
    padding-left: 47px;
  }
}
