.card-brief__title {
  display: inline-block;
  font-family: Rubik, sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 26px;
  color: #1a212f;
  margin-bottom: 22px;
  padding-left: 0;
}

@media (max-width: 1050px) {
  .card-brief__title {
    margin-bottom: 16px;
    font-size: 20px;
    line-height: 24px;
  }
}

@media (max-width: 720px) {
  .card-brief__title {
    margin-bottom: 16px;
    font-size: 20px;
    line-height: 24px;
  }
}
