.filter {
  width: 210px;
  padding-left: 1px;
  padding-right: 20px;
}

@media (max-width: 768px) {
  .filter {
    width: 332px;
    padding: 0 60px 50px 60px;
  }

  .catalog__inner .filter {
    display: none;
  }
}

@media (max-width: 320px) {
  .filter {
    width: 290px;
    padding-left: 25px;
    padding-right: 25px;
  }
}
