.date-range__input-to {
  display: inline-block;
  width: 129px;
  height: 52px;
  border: 1px solid #d7d7d7;
  border-radius: 25px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #1a212f;
  text-align: center;
}

@media (max-width: 768px) {
  .date-range__input-to {
    width: 132px;
  }
}

@media (max-width: 576px) {
  .date-range__input-to {
    width: 107px;
    font-size: 15px;
    line-height: 18px;
  }
}
