.advantage-card__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 91px;
  height: 91px;
  background-color: #fac663;
  border-radius: 50%;
  margin-bottom: 29px;

  img {
    width: 55px;
    height: 55px;
  }
}

@media (max-width: 768px) {
  .advantage-card__icon {
    width: 75px;
    height: 75px;
    margin-bottom: 23px;

    img {
      width: 45px;
      height: 45px;
    }
  }
}

@media (max-width: 320px) {
  .advantage-card__icon {
    width: 60px;
    height: 60px;
    margin-bottom: 23px;

    img {
      width: 35px;
      height: 35px;
    }
  }
}
