.filter-open-button {
  display: none;
  align-self: flex-end;
  width: 121px;
  height: 35px;
  padding-left: 22px;
  border-radius: 25px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  background-color: #fff;
  background-image: url("/img/catalog/filter.svg");
  background-repeat: no-repeat;
  background-position: left 20px center;
  box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);
  cursor: pointer;
}

@media (max-width: 768px) {
  .filter-open-button {
    display: block;
    margin-right: 29px;
  }
}

@media (max-width: 659px) {
  .filter-open-button {
    margin-right: 25px;
  }
}

@media (max-width: 360px) {
  .filter-open-button {
    width: 111px;
    margin-right: 20px;
    background-position: left 15px center;
  }
}
