.footer__legal-info {
  display: flex;
  justify-content: space-between;
  padding-left: 198px;
  padding-right: 198px;
  margin-top: 14px;
}

@media (max-width: 992px) {
  .footer__legal-info {
    padding-left: 45px;
    padding-right: 45px;
  }
}

@media (max-width: 576px) {
  .footer__legal-info {
    margin-top: 11px;
    padding-left: 15px;
    padding-right: 15px;
    flex-direction: column;
  }
}
