.review-card {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  max-width: 470px;
  height: 100%;
  padding: 31px 35px 25px 70px;
  background: #fff;
  box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
  border-radius: 8px;
}

@media (max-width: 768px) {
  .review-card {
    max-width: 442px;
    padding: 31px 33px 25px 70px;
  }
}

@media (max-width: 450px) {
  .review-card {
    max-width: 290px;
    padding: 25px 21px 25px 56px;
  }
}
