.advantages {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 112px;
}

@media (max-width: 768px) {
  .advantages {
    margin-bottom: 81px;
  }
}

@media (max-width: 320px) {
  .advantages {
    margin-bottom: 70px;
  }
}
