.promo {
  height: 504px;
  background-image: url("/img/main-cat.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding-top: 150px;
  margin-bottom: 131px;
}

@media (max-width: 768px) {
  .promo {
    height: 868px;
    background-image: url("/img/main-cat-tablet.webp");
    padding-top: 137px;
    margin-bottom: 111px;
  }
}

@media (max-width: 400px) {
  .promo {
    height: 457px;
    background-image: url("/img/main-cat-mobile.webp");
    padding-top: 48px;
    margin-bottom: 63px;
  }
}
