.text-range_view_catalog {
  margin-bottom: 16px;

  .field-text {
    position: relative;
    width: 100px;
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }

    &__input {
      min-height: 24px;
      border: 1px solid #e1e1e1;
      padding: 0 0 0 25px;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #1a212f;
      border-radius: 0;
    }

    &__name {
      position: absolute;
      top: 5px;
      left: 5px;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #1a212f;
    }
  }
}
