.advantage-card__title {
  font-family: Rubik, sans-serif;
  font-size: 22px;
  line-height: 26px;
  font-weight: 500;
  letter-spacing: -0.2px;
  color: #1a212f;
  margin-bottom: 12px;
}

@media (max-width: 768px) {
  .advantage-card__title {
    font-size: 20px;
    line-height: 28px;
    letter-spacing: -0.2px;
    margin-bottom: 11px;
  }
}

@media (max-width: 320px) {
  .advantage-card__title {
    font-size: 18px;
    line-height: 25px;
    letter-spacing: -0.5px;
    margin-bottom: 11px;
  }
}
