.date-range__title {
  margin-right: 15px;
  display: inline-block;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #1a212f;
}

@media (max-width: 768px) {
  .date-range__title {
    font-size: 15px;
    line-height: 18px;
  }
}

@media (max-width: 576px) {
  .date-range__title {
    margin-right: 170px;
    margin-bottom: 11px;
  }
}
